import {check_is_keywords} from "../../utils/utils_mysql_keywords_check";

export function table_name_check(v) {
  if (v) {
    let regExp = /^[a-z][a-z0-9_]*$/g
    if (!regExp.test(v)) {
      return {
        error: true,
        msg: '!'
      }
    } else {
      if (check_is_keywords(v)) {
        return {
          error: true,
          msg: 'MySQL!'
        }
      }
    }
  }else{
    return {
      error: true,
      msg: '!'
    }
  }
  return {
    error: false,
    msg: null
  }
}

export function table_remarks_check(v) {
  if (!v) {
    return {
      error: true,
      msg: '!'
    }
  }
  return {
    error: false,
    msg: null
  }
}


